        p Las tcnicas creativas implican el desarrollo de mtodos que permiten analizar las situaciones desde diferentes aspectos. Para la mayora de las personas es complicado pensar lateralmente; es decir, salir de lo convencional y proponer soluciones prcticas, pero no comunes. Estas tcnicas incentivan el pensamiento disruptivo, la creacin de nuevas ideas, el mejoramiento de las ya existentes o la adecuacin de soluciones anteriores a problemas actuales.
        p A continuacin, se indican las tcnicas ms utilizadas para estimular la produccin de ideas y su evaluacin, de acuerdo con los descrito por la Fundacin Neuronilla para la Creatividad y la Innovacin (2020):
