    p Esta tcnica fue desarrollada por Alex Osborn (especialista en creatividad y publicidad) en los aos 30 y publicada en 1963 en el libro #[i Applied Imagination]. Es la tcnica ms conocida y utilizada, adems, es la base sobre la que se realizan otras tcnicas. La lluvia de ideas se hace en grupos de trabajo.

