        .bg9.py-2.w-card-text.fw-bold Anlisis morfolgico
    p  Es una de las tcnicas ms valiosas para generar gran cantidad de ideas en un corto perodo de tiempo y se desarroll en los trabajos tecnolgicos de la astrofsica y las investigaciones espaciales llevadas a cabo en los aos cuarenta, como resultado de los adelantos del astrnomo Fritz Zwicky.
    p Es una tcnica combinatoria de ideacin creativa consistente en descomponer un concepto o problema en sus elementos esenciales o estructuras bsicas. Con sus rasgos o atributos se construye una matriz que nos permitir multiplicar las relaciones entre tales partes. As pues, en su forma ms bsica, el anlisis morfolgico no es ms que la generacin de ideas por medio de una matriz. Se realiza llevando a cabo los siguientes pasos:
    .row.justify-content-center.mb-5
