            .row.mt-5.justify-content-center(titulo="Paso 1")
              .col-sm-12.col-lg-6
                h5 Especificar el problema u objetivo.
              .col-sm-12.col-lg-4.d-none.d-lg-block
                img(src='@/assets/curso/temas/tema3/img10.png', alt='')
            .row.mt-5.justify-content-center(titulo="Paso 2")
              .col-sm-12.col-lg-6
                h5 Seleccionar los parmetros del problema.
                p.mb-0 Para determinar si un parmetro es lo suficientemente importante para aadirlo, hay que preguntarse: Seguira existiendo el problema sin el parmetro que estoy pensando para la matriz?.
              .col-sm-12.col-lg-4.d-none.d-lg-block
                img(src='@/assets/curso/temas/tema3/img11.png', alt='')
            .row.mt-5.justify-content-center(titulo="Paso 3")
              .col-sm-12.col-lg-6
                h5 Hacer una lista de las variaciones.
                p.mb-0 Debajo de cada parmetro hay que relacionar tantas variaciones como se deseen. El nmero y variaciones determinarn la complejidad de la matriz. Generalmente, es ms fcil encontrar ideas nuevas dentro de un marco sencillo que de uno complejo. Por ejemplo, una matriz con diez parmetros, cada uno de los cuales tienen diez variaciones y produce 10.000 millones de combinaciones potenciales.
              .col-sm-12.col-lg-4.d-none.d-lg-block
                img(src='@/assets/curso/temas/tema3/img12.png', alt='')
            .row.justify-content-center(titulo="Paso 4")
              .col-sm-12.col-lg-10
                h5 Probar combinaciones diferentes.
                p Cuando la matriz est terminada, hay que hacer recorridos al azar a travs de los parmetros y variaciones, seleccionando uno o ms de cada columna y luego combinarlos de formas completamente nuevas. Se pueden examinar todas las combinaciones de la matriz para ver la manera en que afectan al problema. Si se est trabajando con una que contiene diez o ms parmetros, puede que sea til examinar toda la matriz al azar y luego ir restringiendo gradualmente a porciones que padezcan especialmente fructferas.
                p Es una tcnica muy adecuada para generar ideas en un trabajo exploratorio, pero tambin se distingue por su complejidad para realizarla. Es muy apropiada para:
                ul.lista-ul
                  li.d-flex 
