            h4 Interseccin
            p.mb-0 Se necesita un cicln a domicilio... una tromba de agua... vertical... las paredes sern cilindros que encajan... cuando no nos servimos de ella, formar un asiento o un elemento decorativo...en servicio ser un cilindro...y los chorros de agua partirn del suelo o de las paredes y reconstruirn nuestro cicln...
