        .bg9.py-2.w-card-text.fw-bold Conexiones morfolgicas forzadas
    p Es una propuesta de Koberg y Bagnall que en realidad es la suma de los principios bsicos de dos tcnicas: el Listado de Atributos creado por R. P. Crawford y las Relaciones Forzadas de Charles S. Whiting.
    p Es muy potente porque en una primera fase facilita que surjan ideas sobre aspectos que no habamos tenido en cuenta y en la segunda provoca analogas muy inspiradoras.
