      .col-sm-12.col-lg-11
        p.mb-0 La estrategia creativa es un proceso de desarrollo de ideas para la comunicacin publicitaria, busca que la interaccin entre el cliente y la marca sea lo ms efectiva posible. Adems, incluye varios elementos: objetivos, #[i target] comunicacional y concepto creativo. 
