          .col-sm-12.col-lg-6
            p Bsicamente hay tres grandes grupos generales de objetivos de #[i marketing]. Estos son:
            ol.lista-ol--cuadro
              li.d-flex
                .lista-ol--cuadro__vineta.cs
                  span.fw-bold 1
