          .col-sm-12.col-lg-6
            ul.lista-ul.mb-5
              li.d-flex
                i.fas.fa-check-circle.color-primario
                span La funcin puntual de un objetivo de comunicacin es acentuar o modificar conductas, hbitos o comportamientos en el #[i target] de campaa.

              li.d-flex
                i.fas.fa-check-circle.color-primario
                span Esencialmente un objetivo de comunicacin debe informar adecuadamente, persuadir a travs de una propuesta diferencial, dar a conocer claramente dicha propuesta y comunicar correctamente el mensaje; este ltimo en la estrategia de comunicacin est representado por la promesa y el #[i reason why] (soporte de promesa o argumentacin).
      .p-4(titulo="Objetivos de medios")
        .row.justify-content-center.align-items-center.mb-5
          .col-sm-12.col-lg-6.d-none.d-lg-block
            img(src='@/assets/curso/temas/tema5/img5.svg', alt='')
            
          .col-sm-12.col-lg-6
            p Este tipo de objetivos estn relacionados directamente con la planificacin de acciones #[i Above The Line] (ABT), acciones en medios masivos. Estos objetivos esencialmente son tres:
            ol.lista-ol--cuadro
              li.d-flex
                .lista-ol--cuadro__vineta.cs
                  span.fw-bold 1
                span #[b Lograr tantos Puntos Brutos de #[i Rating] (PBR) o #[i Gross Rating Point] (GRP):] expresa la presin publicitaria ejercida por una pauta, ya que, en esta variable, intervienen tanto cobertura como frecuencia. Se miden semanalmente y deben estar indicados en la pauta.

              li.d-flex
                .lista-ol--cuadro__vineta.cs
                  span.fw-bold 2
                span #[b Alcanzar determinada cobertura neta:] se refiere a la cantidad de personas expuestas por lo menos una vez (ms exactamente un minuto) a los comerciales de una pauta, pero solo de nuestro #[i target] objetivo. La cobertura tambin se conoce como #[i Reach] y se expresa en porcentajes.


              li.d-flex
                .lista-ol--cuadro__vineta.cs
                  span.fw-bold 3
                span #[b Obtener determinada frecuencia (tasa de repeticin):] expresa la cantidad de veces que una persona ha estado expuesta a los comerciales de una pauta.
      .p-4(titulo="Objetivos <i>Below The Line</i> (BTL)")
        .row.justify-content-center.align-items-center.mb-5
