    Separador
    #t_5_2.titulo-segundo.color-secundario
      h2  5.2 #[i Target] comunicacional
    .row.justify-content-center.align-items-center.mb-5
      .col-sm-12.col-lg-8
        .cajon.color-secundario.p-4.mb-4
          p.mb-0 Este es un concepto utilizado de forma casi exclusiva por creativos y algunas agencias de publicidad; es un paso ms all de la segmentacin de mercados y permite conocer de forma individual a los consumidores de un producto o servicio, obteniendo de ellos informacin detallada de su comportamiento, gustos, intereses y deseos.
        p.mb-3 El desarrollo del #[i target] comunicacional se realiza con tcnicas de investigacin cualitativa y lo ms adecuado es que el proceso sea llevado a cabo por personas con estudios en psicologa del consumidor, antropologa o sociologa. El mtodo ms preciso y utilizado para el desarrollo de un #[i target] comunicacional es la observacin.
        p.mb-3 El fin del desarrollo de este punto, es obtener toda la informacin posible del mercado objetivo a travs de la individualizacin de los consumidores; en ese orden de ideas, es posible que sea necesario realizar varios #[i targets] comunicacionales y, por lo tanto, varias versiones del mensaje, de acuerdo con las caractersticas de cada uno de ellos.
        p.mb-0 A continuacin, se indica el proceso para realizar el #[i target] comunicacional:
      
      .col-sm-12.col-lg-4.d-none.d-lg-block
        img(src='@/assets/curso/temas/tema5/img7.png', alt='')
    .d-flex.mb-4.align-items-center
        img.w-card-icon(src="@/assets/curso/temas/tema3/img51.svg")
        .bg9.py-2.w-card-text.fw-bold Segmentacin del mercado
    .row.justify-content-center.align-items-center.mb-5
      .col-sm-12.col-lg-7
        p Este proceso ya se ha llevado a cabo anteriormente, en este caso, se debe considerar el mercado real y el anlisis psicogrfico realizado. Dicho mercado debe dividirse en nichos o grupos especficos utilizando una recta numrica sencilla, basada en variables bsicas. 
        p #[b Por ejemplo:] si el mercado real est compuesto por mujeres de 20 a 35 aos de edad, pertenecientes a los estratos 3, 4 y residentes de las principales ciudades de Colombia, se elabora la siguiente grfica, la cual se ir modificando a medida que se realice el anlisis correspondiente en este tema.
      .col-sm-12.col-lg-5
        .titulo-sexto.color-secundario
          h5 Figura 1.
          span
            |  Segmentacin del mercado
        figure
          img.img-a(src='@/assets/curso/temas/tema5/img8.svg', alt='La grfica presenta la segmentacin del mercado con base al anlisis de las edades y los estratos de las mujeres en las ciudades de Colombia, donde la edad mnima es 20 aos y la mxima 35 aos.')
          
    .row.justify-content-center.align-items-center.mb-5
      .col-sm-12.col-lg-4.d-none.d-lg-block
        img(src='@/assets/curso/temas/tema5/img9.svg', alt='')

      .col-sm-12.col-lg-8
        p Con esta informacin es hora de tomar decisiones en cuanto a cul o cules sern los #[i targets] comunicacionales. En este punto hay que tener en cuenta varios aspectos: el presupuesto de la campaa, si este es limitado es posible que deba realizarse un solo modelo de anuncio que abarque todo el mercado objetivo; las caractersticas del mercado al que va dirigida la campaa, en el caso del ejemplo es necesario preguntarse si existen coincidencias psicogrficas en las mujeres de 20 a 35 aos que se puedan incluir en un anuncio y las identifique a ambas, o en el desarrollo del mensaje incluir temticas que sean compatibles con todas.
        .cajon.color-acento-contenido.p-4
          p.mb-0 Para el desarrollo del #[i target] comunicacional se deben entonces definir a partir de ese anlisis psicogrfico, los #[i targets] que se van a realizar; para ello, se seguir an ms el mercado real teniendo en cuenta gustos, intereses, formas de pensar, proyectos a futuro, etc.
    .titulo-cus.mb-4.w-1
      h4 Seleccin
    .row.justify-content-center.align-items-center.mb-5
      .col-sm-12.col-lg-8
        p.mb-3 En el ejemplo de las mujeres de 20 a 35 aos se pueden encontrar varias diferencias que las separan en cuanto a prioridades, estilo de vida y motivaciones, temas muy relevantes para un proceso de comunicacin.
        p.mb-3 Una mujer de 20 aos est comenzando a vivir, posiblemente est estudiando una carrerea o abrindose paso en el mundo laboral; si bien tiene relaciones sentimentales, tal vez no sea una prioridad el tener hijos, casarse y conformar una familia, puesto que sus motivaciones sern pasarla bien con sus amigos, divertirse, salir, viajar o conocer. Sus principales preocupaciones radican en qu ponerse, con quin y a dnde ir de fiesta, ganar el semestre de la universidad y cumplir con algunas obligaciones en el hogar, que an comparte con sus padres.
        p.mb-0 La mujer de 35 aos, por su parte tiene otras motivaciones, si bien quiere viajar y conocer, tal vez sus expectativas sean mayores en funcin de su capacidad de endeudamiento; a esta edad por lo general ya se tienen hijos y una familia conformada, sus prioridades por lo tanto son sus hijos, esposo y hogar. A los 35 quizs se tengan mayores responsabilidades laborales, lo que implica un estilo de vida ms atareado y complejo; situacin que puede variar de acuerdo con el estrato social, situacin que debe ser de anlisis.
      .col-sm-12.col-lg-4.d-none.d-lg-block
        img(src='@/assets/curso/temas/tema5/img10.png', alt='')
    .row.justify-content-center.mb-5
      .col-sm-12.col-lg-7
        .cajon.color-acento-contenido.p-5.mb-cus
          p.mb-0 De lo anterior, se puede concluir que el desarrollo de un anuncio publicitario dirigido a mujeres de 20 a 35 aos debe tener varias versiones en cuanto al desarrollo del mensaje. Los #[i targets] comunicacionales entonces deben seleccionarse en funcin de sus caractersticas psicogrficas; para ello, se divide la recta numrica en los grupos que el anlisis haya arrojado, cabe anotar que esta divisin sigue siendo un aspecto netamente subjetivo:
      .col-sm-12.col-lg-5
        .titulo-sexto.color-secundario
          h5 Figura 2.
          span
            |  Seleccin del #[i target]
        figure
          img.img-a(src='@/assets/curso/temas/tema5/img11.svg', alt='La seleccin del target es con base a las caractersticas psquicas donde posteriormente se dividen en grupos por edades.')
          
    p En este caso se han seleccionado tres grupos, uno de 20 a 22 aos; se tuvo en cuenta que en promedio a los 22 aos se est terminando una carrera profesional y comenzando una vida laboral activa, lo que implica que la conducta es muy similar hasta esa edad. De los 23 a los 30 se est consolidando una vida laboral, profesional y social, en estas edades se suele viajar, conocer personas y consolidar relaciones sentimentales. A los 31 se comienza la vida familiar, los proyectos econmicos, inversiones y negocios. A cada uno de ellos es necesario realizar el #[i target] comunicacional, ya sea para realizar un anuncio dirigido a los tres o para varias versiones; para el primer caso esto ayuda a encontrar coincidencias y para el segundo a determinar el mensaje para cada uno.
    p.mb-5 El desarrollo de #[i target] comunicacional est por realizarse, lo siguiente que se debe hacer es comenzar a describirlo; como se dijo anteriormente, este proceso implica aplicar una tcnica de investigacin, lo recomendable es observar y entrevistar a personas que renan las caractersticas del grupo seleccionado.
    .titulo-cus.mb-4.w-2 
      h4  Descripcin del #[i target]  
    .row.justify-content-center.align-items-center.mb-5
      .col-sm-12.col-lg-4.d-none.d-lg-block
        img(src='@/assets/curso/temas/tema5/img12.svg', alt='')
      .col-sm-12.col-lg-8
        p La redaccin del #[i target] comunicacional es descriptiva, de esta forma facilita el proceso creativo, toda vez que se trata de contar la historia del mercado objetivo. Al desarrollo de este proceso se le llamar: una semana en la vida de; una semana porque durante este tiempo se puede describir en casi todos los aspectos el estilo de vida del #[i target], agregndole algunos elementos que se pueden incorporar dentro de la misma historia como los sueos, deseos, proyectos y datos de compra y consumo.
        .cajon.color-acento-contenido.p-4
            p.mb-0 El #[i Target] Comunicacional (TC) requiere comenzar con un nombre, este debe ser el ms comn o uno de los ms comunes para el tipo de persona que se est describiendo; para esto deben tenerse en cuenta las tendencias; por ejemplo: en mujeres de 20 a 23 aos un nombre muy comn es Daniela.

    p.mb-5 Una vez se ha dado un nombre a la protagonista, es necesario comenzar a describir el da a da de la persona, incluyendo todos los detalles posibles: hora de levantarse, qu hace inmediatamente abre sus ojos, cuntas veces suena la alarma, si se baa inmediatamente, qu hace en el bao, si cepilla los dientes mientras se baa o cuando sale, etc. Entre ms detalles se mencionen, mayores posibilidades habr de realizar una estrategia creativa exitosa. La descripcin debe incluir toda la rutina, hasta que se acuesta nuevamente.
    .row.justify-content-center.align-items-center.mb-5
      .col-sm-12.col-lg-4.d-none.d-lg-block
        img(src='@/assets/curso/temas/tema5/img15.png', alt='')

      .col-sm-12.col-lg-8
        .row.justify-content-center.align-items-center.mb-4
          .col-sm-12.col-lg-2.d-none.d-lg-block
            img.w-75.m-auto(src='@/assets/curso/temas/tema5/img13.svg', alt='')
          .col-sm-12.col-lg-10
            p.mb-0 Tal vez la rutina sea muy similar entre semana, pero de acuerdo con las investigaciones es importante tener en cuenta todas las actividades que el grupo seleccionado realice.
        .cajon.color-secundario.p-4.mb-4
            p.mb-0 #[b Por ejemplo:]  se encontr que este tipo de mujeres van al gimnasio tres veces por semana o estn realizando cursos de arte una vez por semana, acuden al saln de belleza los mircoles o lo hacen cada 15 das; todos los detalles deben incluirse.
        .row.justify-content-center.align-items-center.mb-4
          .col-sm-12.col-lg-2.d-none.d-lg-block
            img.w-75.m-auto(src='@/assets/curso/temas/tema5/img14.svg', alt='')
          .col-sm-12.col-lg-10
            p.mb-0 El fin de semana, por lo general hay un cambio de rutina, puede ser que estn realizando estudios de postgrado o actividades deportivas y recreativas. Los planes, proyectos e intereses deben incluirse en la historia como situaciones del da a da.
        .cajon.color-acento-botones.p-4.mb-4
          p.mb-0 #[b Por ejemplo:] decir que la protagonista est planeando un viaje con su novio y deciden hacer las reservaciones de hoteles con varios meses de anticipacin a travs de sitios #[i web] especializados. Tambin deben incluirse detalles de la msica que escuchan, la comida que piden o preparan.

    Separador
    #t_5_3.titulo-segundo.color-secundario
      h2 5.3 El concepto creativo
    p El concepto creativo es la base principal para el desarrollo del mensaje de la campaa, este determina la forma que tendr el mensaje que se dirigir al mercado objetivo; es en este punto donde debe darse todo el despliegue creativo.
    p Para el desarrollo del concepto, es indispensable tener en cuenta las siguientes recomendaciones:
    .row.justify-content-center.mb-5
      .col-sm-12.col-md-6.col-lg-4.mb-cus
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema3/img15.svg').d-none.d-lg-block
          .tarjeta.bgimg3.w-100
            .text-center.p-4
              p.title-card.bg3.text-white.mb-4 Originalidad  
              p.mt-3.mb-5.pb-5 La forma como se comunique la marca con su pblico objetivo debe ser nica, no parecerse a otro producto y menos an si es de la competencia.
      .col-sm-12.col-md-6.col-lg-4.mb-cus
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema3/img16.svg').d-none.d-lg-block
          .tarjeta.bgimg3.w-100
            .text-center.p-4
              .title-card.bg4 Claro
              p.mt-3.mb-5.pb-5 El concepto debe ser muy fcil de identificar para los consumidores y en lo posible que la relacin de este con la marca y sus beneficios sea evidente.
      .col-sm-12.col-md-6.col-lg-4.mb-cus
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema3/img17.svg').d-none.d-lg-block
          .tarjeta.bgimg3.w-100
            .text-center.p-4
              p.title-card.bg3.text-white.mb-4 Creativo
              p.mt-3.mb-5.pb-5 Por obvias razones el concepto de una estrategia de comunicacin debe ser creativo, la creatividad permite no solo que la idea sea llamativa, sino que se destaque frente a otras.

    .d-flex.mb-3.align-items-center
        img.w-card-icon(src="@/assets/curso/temas/tema3/img51.svg")
        .bg9.py-2.w-card-text.fw-bold Estructura del concepto creativo
    p.mb-5 El concepto es tambin un proceso que permite al mensaje tener ms definicin y a la estrategia creativa un norte, por eso es importante que estos elementos se desarrollen teniendo en cuenta todos los parmetros ya establecidos en el #[i brief] y en los antecedentes de la marca.
    .titulo-cus.mb-4.w-3
          h4   Tipo
    p Cada campaa o estrategia de comunicacin que se realice est ligada a un momento especfico de la marca, producto, servicio o empresa. Cada producto o servicio atraviesa unas etapas durante su proceso comercial y cada una de ellas comprende un proceso de comunicacin especial. Estas etapas estn consignadas en el ciclo de vida del producto.
    .titulo-sexto.color-secundario
      h5 Figura 3.
      span
        |  Ciclo de vida del producto
    .row.justify-content-center.mb-5
      .col-sm-12.col-lg-10
        figure
          img.img-a(src='@/assets/curso/temas/tema5/img16.svg', alt='Cada campaa se lleva a cabo de acuerdo a un ciclo de vida especifico del producto: I+D+I: prueba de mercado. Introduccin: lanzamiento. Crecimiento: mantenimiento. Madurez: recordacin. Declive: relanzamiento')
          
    p En la grfica se pueden evidenciar las etapas y el tipo de comunicacin que se requiere para cada una de ellas:
    .row.justify-content-center.mb-4
      .col-sm-12.col-lg-3.d-none.d-lg-block
        img.img-a(src='@/assets/curso/temas/tema5/img17.svg')
      .col-sm-12.col-lg-9
       TabsA.color-acento-botones.mb-5
        .tarjeta.color-acento-botones--borde.p-4(titulo="I+D+I")
          p.mb-0 Esta es la etapa de ideacin del producto o servicio, en este punto no hay comunicacin masiva; sin embargo, es posible que en el desarrollo de las pruebas de mercadeo se realicen estrategias de comunicacin dirigidas a segmentos muy especficos, con el fin de probar la respuesta del grupo objetivo al nombre del producto, al empaque o al estilo de comunicacin que han planteado para hacer ajustes antes de su introduccin definitiva al mercado.
        .tarjeta.color-acento-botones--borde.p-4(titulo="Introduccin")
          p.mb-0 En esta etapa el producto es puesto en oferta; es decir, se pone a disposicin de los compradores y consumidores. La comunicacin que se realiza en este punto es de lanzamiento; aqu es necesario ser enftico en sus beneficios, tanto el principal como los secundarios y la comunicacin se realiza utilizando todos los medios posibles y el concepto debe conectar con el consumidor. En ocasiones y cuando las marcas tienen un alto nivel de posicionamiento, es posible que realicen un #[i Teaser], es decir, una etapa inicial con expectativa, generando inquietud en los consumidores y con ello, generar impulso de compra.
        .tarjeta.color-acento-botones--borde.p-4(titulo="Crecimiento")
          p.mb-0 En esta etapa del ciclo de vida del producto, la comunicacin debe ser constante y enftica en los beneficios; en este proceso se est convenciendo a ms miembros del mercado objetivo que se adhieran a la marca y se queden con ella. En este punto se realiza comunicacin de mantenimiento, como su nombre lo indica debe mantenerse cautivo el mercado ganado y ganar ms para lograr retorno sobre la inversin.
        .tarjeta.color-acento-botones--borde.p-4(titulo="Madurez")
          p.mb-0 En esta etapa ya hay un mercado cautivo y suficiente para que el producto genere ganancias, ya es reconocido y aceptado por el mercado. En este punto se realiza comunicacin de recordacin, la cual implica que haya mucha interaccin entre la marca y sus consumidores, comunicacin sencilla, breve y directa con presencia de la marca, el fin es que el mercado no olvide la marca.
        .tarjeta.color-acento-botones--borde.p-4(titulo="Declive")
          p.mb-0 Esta etapa se produce por varias razones, la principal es la llegada al mercado de productos similares que han captado la atencin de los consumidores con otros beneficios, menor precio u otras presentaciones; en ese punto, en ocasiones es necesario realizar cambios al producto para adaptarlo a las nuevas preferencias del pblico objetivo, esto implica que el ciclo de vida comienza nuevamente y es necesario realizar un relanzamiento, las caractersticas de este tipo de comunicacin son similares a las del lanzamiento, con la diferencia que el posicionamiento del producto va a permitir llegar de una forma ms rpida al crecimiento y la madurez; es decir, las fases de la comunicacin sern ms cortas.
    .titulo-cus.mb-4.w-3
          h4   Tono
    p.mb-5 El diseo del mensaje es la parte ms complicada y demorada del proceso, el concepto es la base para su elaboracin y cada parte de l contribuye poco a poco al desarrollo. El tono constituye un elemento muy importante en la elaboracin del mensaje y desarrollo de la idea, porque determina en gran medida la empata que la marca tiene con el grupo objetivo y muestra rasgos muy relevantes de la personalidad de la marca. Es necesario definir el tono de la comunicacin teniendo en cuenta todos esos elementos, pues el mercado suele ser susceptible a ciertos aspectos que pueden llegar a ser ofensivos o transgredir la forma de pensar de algunos miembros del mercado.
    SlyderF.mb-5(columnas="col-lg-6 col-xl-4")
