        h5.text-center.mt-4 Comunicacional
        p.mt-3.text-center.txt-sistema Es directo, sencillo y plano; implica poco despliegue creativo y es muy apto para comunicar ofertas de un solo da o dar informacin fugaz de alguna eventualidad del producto o servicio. Este tono de comunicacin es poco persuasivo.
      .tarjeta.color-secundario.p-4
