            img(src='@/assets/curso/temas/tema5/img21.svg')
        h5.text-center.mt-4 Racional
        p.mt-3.text-center.txt-sistema Si bien un bajo porcentaje de las compras que se realizan, se hacen de forma racional, este es un tono muy persuasivo, pero sobre todo disuasivo. Es muy eficaz en comunicacin institucional y para campaas de bien pblico en las que hay que convencer al pblico objetivo que realice una accin especfica para evitar ciertas consecuencias; por ejemplo, las campaas que promueven el apoyo a fundaciones o las que invitan a moderar el consumo de alcohol. Este tono genera algo grado de recordacin.
      .tarjeta.color-secundario.p-4
