        p El #[i insight] es una palabra que  proviene de la psicologa, y se utiliza cuando el psicoterapeuta puede entender con claridad el problema (motivo de consulta) de su paciente luego de largas sesiones de auscultarlo y conversar con l. Ese momento, el de la revelacin, es propiamente un #[i insight] pues el terapeuta comprende el origen del problema y las motivaciones ms ocultas del paciente para actuar de la forma que hace.
